import React from 'react';
import logo from './logo.svg';
import { connect } from 'react-redux'
import { Route } from 'react-router-dom'
import './App.scss';


import {Header, Footer, RegistrationForm, LoginForm} from './components'
import {Main, PeopleHelp, Forum, AboutUs, ContactUs} from './containers'


const App = () => {
  return (
    <div className="App">
      
      <Header />
      <Route path='/login' component={LoginForm} />
      <Route path='/registration' component={RegistrationForm} />
      <Route exact path='/' component={Main} />
      <Route exact path='/help' component={PeopleHelp} />
      <Route exact path='/forum' component={Forum} />
      <Route exact path='/about' component={AboutUs} />
      <Route exact path='/feedback' component={ContactUs} />

      <Footer />
    </div>
  );
}

export default App;
