import React, { useState } from 'react';
import { Form, Icon, Input, Button } from 'antd';
import { PasswordInput } from 'antd-password-input-strength';
import { NavLink } from 'react-router-dom';
import './registrationform.scss';

const RegistrForm = (props) => {
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  function handleSubmit(e) {
    e.preventDefault();
    if (password === confirmPassword) {
      props.form.validateFields((err, values) => {
        if (!err) {
          console.log('Получение значей формы: ', values);
    
    } else {
      console.log('Пароль не подходит')
    }
  });
}
  

  };
  const { getFieldDecorator } = props.form;
  return (
    <div className="form-container">
      <div className="form-wrap">


        <Form onSubmit={handleSubmit} className="login-form">

          <h2 className="form-title">Зарегистрируйтесь сейчас!</h2>
          <Form.Item>
            {getFieldDecorator('username', {
              rules: [{ required: true, message: 'Пожалуйста введите ваше имя!' }],
            })(
              <Input
                prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                placeholder="ФИО"
              />,
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('email', {
              rules: [{ required: true, message: 'Введите ваш email!' }],
            })(
              <Input
                prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />}
                placeholder="email"
              />,
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('password', {
              rules: [{ required: true, message: 'Придумайте пароль!' }],
            })(
              <PasswordInput
                prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                inputProps={{
                  placeholder: "password",
                  settings: {
                    height: 5
                  }
                }}
                value={password}
                onChange={e => setPassword(e.target.value)}
              />,
            )}
          </Form.Item>
          <Form.Item>
            <PasswordInput
              prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
              inputProps={{
                placeholder: "Подтвердите пароль",
                settings: {
                  height: 5
                }
              }}
              value={confirmPassword}
              onChange={e => setConfirmPassword(e.target.value)}
            />

          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit" className="login-form-button-reg">
              Регистрация
            </Button>
            <span className="form-text">Или</span> <NavLink to="/login" className="form-text" activeClassName="selected">
              Войти!
               </NavLink>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
}

const RegistrationForm = Form.create({ name: 'register_form' })(RegistrForm);

export default RegistrationForm;




