import React from 'react'
import {connect} from 'react-redux'
import {NavLink} from 'react-router-dom'
import AccessibleForwardIcon from '@material-ui/icons/AccessibleForward'

import './header.scss'


const Header = (props) => {
    return (
        <header className="header">
            <div className="flex">
                <NavLink to='/'><div className="logo">Invo</div> </NavLink>
                <nav>
                    <ul className="navBar">
                        {
                            props.listItems.map((item, index) => {
                                return <li key={index} className="navItem"><NavLink  to={item.path} activeStyle={{ color: 'rgb(75,163,160)'}} className="nav_href">{item.name}</NavLink></li>
                            })
                        }
                    </ul>
                </nav>
            </div>
            <NavLink to='/login' className="enter">
                <AccessibleForwardIcon fontSize="large" />
                <p className="enter__text">Войти</p>
            </NavLink>
        </header>
    )
}


const mapStateToProps = state => {
    return {
      listItems: state.header.header
    };
  };
  
  export default connect(mapStateToProps)(Header);
  