import React, {useState} from 'react'

import './filterbar.scss'

import {Radio, Select} from 'antd'
const Option = Select.Option



const FilterBar = () => {
    const [value, setValue] = useState('available')

    const handleChange = (e) => {
        setValue(e.target.value)
    }
    return (
        <div className="filterbar-container">
           <Radio.Group size={'large'} value={value} onChange={handleChange} >
                <Radio.Button value="available">Доступные</Radio.Button>
                <Radio.Button value="medium">Среднедоступные</Radio.Button>
                <Radio.Button value="inaccessible">Недоступные</Radio.Button>
            </Radio.Group>

            <Select
                size={'large'}
                defaultValue="Выберите место"
                style={{ width: 200 }}
                >
            <Option key={0} >Аптеки</Option>
            <Option key={1}>Кафе</Option>
            <Option key={2}>Кинотеатры</Option>
            <Option key={3}>Разное</Option>
        </Select>

        </div>
    ) 
}


export default FilterBar