import React from 'react'

import './pharmcard.scss'

const PharmCard = (props) => {
    return (
        <div className="pharm-card" onClick={() => {props.onclick(props.item)}}>
            <div className="pharm-card__img-container">
                <img src={props.img.indexOf('jpg') === -1 ? './assets/img/apteka.jpg' : props.img} alt="img" className="pharm-card__img" />
            </div>
            <div className="pharm-card__container">
                <div className="pharm-card__name">
                    <p>{props.name}</p>
                </div>
                <div className="pharm-card__description">
                    <ul className="pharm-card__description__list">
                        <li className="pharm-card__description__item">Телефон</li>
                        <li  className="pharm-card__description__item">{props.phone}</li>
                    </ul>
                </div>
            </div>
        </div>
    )
}


export default PharmCard