import React, { useState } from 'react';


import './helpform.scss'
import { 
    Select
 } from 'antd'

 const Option = Select.Option

const HelpForm = () => {
    const [formMode, setFormMode] = useState(false);


    const checkHendler = () => {
        setFormMode(!formMode)
    }

    return (
        <div>
            
            <div className={!formMode ? "help_form_form " : "help_form_form hide choise"}>
                <h3 className="choise_title" >Выберете</h3>
                <label className="choise_inv_or_not">
                    <input type="checkbox" className="checkbox" onClick={checkHendler} />Мне нужна помощь
            </label>
                <label className="choise_inv_or_not">
                    <input type="checkbox" className="checkbox" onClick={checkHendler} />Я предлагаю помощь
            </label>
            </div>
            <form className={formMode ? "help_form_form " : "help_form_form hide"} action="#">
            <img onClick={checkHendler} src="./assets/back-arrow.png" alt="back-arrow" className="back-arrow" />

                <h3 className="request_form_title">Форма отправки заявки</h3>
                <input className="request_input" type="text" name="FIO" placeholder="ФИО" />
                <input className="request_input" type="text" name="Phone" placeholder="Номер телефона" />

                <input className="request_input" type="text" name="text" placeholder="Аддрес" />
                <Select
                    size={'large'}
                    defaultValue="Выберите район"
                    style={{ width: 200, marginBottom: 30 }}
                >
                    <Option key={0}>Центральный</Option>
                    <Option key={1}>Совецкий</Option>
                    <Option key={2}>Партизансикй</Option>
                    <Option key={3}>Фрунзенский</Option>
                </Select>
                <div className="request_checkBox">
                    <div className="block">
                        <input type="checkbox" name="wheelchair" title="Есть коляска" />Есть коляска
                            </div>
                    <div className="block">
                        <input type="checkbox" name="big_car" title="Большая машина" />Большая машина
                            </div>
                </div>
                <textarea className="request_input textarea" placeholder="Комментарий"></textarea>
                <button className="request_btn">Отправить</button>




            </form>
        </div>
    )
}

export default HelpForm