import React from 'react'

import './carddescription.scss'

import {MapContainer} from '../'

const CardDescription = (props) => {
    const {item, active} = props

    const coords = {
        lat: item.coords[0],
        lng: item.coords[1]
    }
    console.log(coords)
    return (
        <div className={ active ?  'carddesc active' : 'carddesc' } >
            <div className="carddesc__top" >
                <img src={item.srcImg.indexOf('jpg') === -1 ? './assets/img/apteka.jpg' : item.srcImg} className="carddesc__top-img"  />
                <div className="carddesc__short-desc" >
                    <div className="carddesc__short-desc_top">
                        <a href={item.detailHref} target="_blank" className="carddesc__title main">{item.name}</a>
                        <p>Телефон: <a className="carddesc__phone" href={'tel:'+ item.phone}>{item.phone}</a></p>
                    </div>

                    <div className="carddesc__short-desc_bot">
                        <h3 className="carddesc__title">Время работы</h3>
                        <table>
                            {
                                item.workTime.map(e =>  (
                                        <tr>
                                            <th className="carddesc__workday" >{e.day}</th>
                                            <td className="carddesc__worktime">{e.time}</td>
                                        </tr>
                                    )
                                )
                            }
                            
                        </table>
                    </div>
                    
                </div>
            </div>
           

            <h3 className="carddesc__address" >{item.address}</h3>

            <div  className="carddesc__map">
                     <MapContainer initialCoords={coords} />
            </div>
            <div onClick={props.closeCard} className="carddesc__close">X</div>
        </div>
    )
}

export default CardDescription