import React , { useEffect }from 'react';

import { useForm } from 'react-hook-form'
import { NavLink } from 'react-router-dom';
import './loginform.scss';


const LoginForm = (props) => {

 
  const { register, handleSubmit, errors} = useForm();

  const onSubmit = data => {
    console.log(data)

    const b = new FormData()
    b.append("username", data.username)
    b.append("password", data.password)

    fetch('192.168.43.242:8000/auth/', {
      method: 'POST',
      body: b
    }).then(res => console.log(res.json()))
  }
  
  return (
    <div className="form-container">
      <div className="form-wrap">
        <form onSubmit={handleSubmit(onSubmit)} className="login-form">

          <h2 className="form-title">Войдите на сайт</h2>
          <label  className="login-label">Введите login</label>
                <input type="text" name="username" className="login-input" ref={register({ required: true, maxLength: 30 })} />
                {errors.name && errors.name.type === 'required' && <span>This is required</span>}
                {errors.name && errors.name.type === 'maxLength' && <span>Max length exceeded</span> }
                <label  className="login-label">Введите пароль</label>
                <input type="text" name="password" className="login-input"  ref={register({ required: true, maxLength: 30 })} />
                {errors.name && errors.name.type === 'required' && <span>This is required</span>}
                {errors.name && errors.name.type === 'maxLength' && <span>Max length exceeded</span> }
                <input type="submit" className="login-submit" />
              <span className="form-text">Или</span> <NavLink to="/registration" className="form-text" activeClassName="selected">
              Зарегистрироваться!
               </NavLink>
         </form>
      </div>
    </div>
  );
}



export default LoginForm;




